    reqHeartBeat() {
        // console.log("----------", WmSocket.getInstance().isConnected(), ServerCtr.GetInstance().loginState);

        if (WmSocket.getInstance().isConnected() == false) {
            // uiManager.instance.showDialog(Const.Dialogs.net_prompt, )
            // Notifications.emit(GDef.wdEvent.showTip, {msg:""})
            Http.getInstance().httpPost(tyqSDK.getUrl().httpUrl + GNetConst.Http_isAlive, {}, (res: any) => {
                // cc.log("", res)
                if (res != -1) {
                    TimeCtr.GetInstance().UpdateServerTime(res.serverTime);
                    director.emit("ws_reconnect");
                }
            }, () => {
                ServerCtr.GetInstance().loginState = GLoginState.loginFail;
            });
        } else {
            if (ServerCtr.GetInstance().loginState == GLoginState.loginFail) {
                //
                ServerCtr.GetInstance().wxLoginBegin();
            } else {
                this.heartbeatCheck()
            }
        }
    }


