import { tyqSDK } from "../../tyqSDK";

export interface IPos {
    left?: number;
    right?: number;
    top?: number;
    bottom?: number;
    centerX?: number;
    centerY?: number;
}

export enum EType {
    // * @param type 1234
    rect = 1,
    horizontal,
    vertical,
    grid,
}
export class WXCustomAd {

    /**id	 */
    public static _customc_rect_adunit = [];

    /**id	 */
    public static _customc_h_adunit = [];

    /**id	 */
    public static _customc_v_adunit = [];

    /**id	 */
    private static _customc_one_adunit = [];

    /** */
    private static _ads = {};

    /** */
    private static _waits = [];

    private static sysInfo = null;

    private cb: any;
    static cb: Function;
    /**
     * id
     * @param rectid id
     * @param hid id
     * @param vid id
     * @param oneid id
     */
    static setadunit(rectid, hid, vid, oneid) {
        this._customc_rect_adunit = rectid;
        this._customc_h_adunit = hid;
        this._customc_v_adunit = vid;
        this._customc_one_adunit = oneid;
    }

    /**
     * 
     * @param flag 
     * @param type 1234
     * @param pos (leftrighttopbottomcenterXcenterY)
     * @param adSize 
     * @param extraId id
     * @param failcb:1004 
     * @returns 
     */
    static createCustomAd(flag, type: EType, pos: IPos, adSize: { width: number, height: number }, extraId?: any, failcb?: Function) {
        if (!window.wx) return;
        if (!this.sysInfo) {
            this.sysInfo = wx.getSystemInfoSync();
        }
        let version = this.sysInfo.SDKVersion;
        let ad = null;
        let id = "";
        let self = this;

        if (failcb) {
            this.cb = failcb;
        }

        if (type == 1) {
            id = this._customc_rect_adunit[this.rand(0, this._customc_rect_adunit.length - 1)];
        } else if (type == 2) {
            id = this._customc_h_adunit[this.rand(0, this._customc_h_adunit.length - 1)];
        } else if (type == 3) {
            id = this._customc_v_adunit[this.rand(0, this._customc_v_adunit.length - 1)];
        }
        else if (type == 4) {
            id = this._customc_one_adunit[this.rand(0, this._customc_one_adunit.length - 1)];
        }
        if (extraId && extraId !== "") {
            if (typeof extraId == "string") {
                id = extraId;
            } else if (extraId instanceof Array) {
                id = extraId[this.rand(0, extraId.length - 1)];
            }
        }

        //id
        if (id == "") return;

        tyqSDK.log("", flag, this._ads, this._waits);
        //
        if (this._ads[flag]) {
            ad = this._ads[flag];
            tyqSDK.log("");
            if (!ad.isShow()) ad.show();
        }
        else if (this.compareVersion(version, '2.11.1') >= 0) {
            tyqSDK.log("" + flag, "id:" + id, "pos:", pos);
            let p = self.getPos(adSize, pos);
            ad = wx.createCustomAd({
                adUnitId: id,
                adIntervals: 30,
                style: {
                    top: p.top,
                    left: p.left,
                    width: adSize.width, // 
                    fixed: false // fixed 
                }
            })

            ad.onLoad(() => {
                self._ads[flag] = ad;
                let index = self._waits.indexOf(flag);
                tyqSDK.log("" + flag, self._waits, index);
                if (index == -1)
                    ad.show();
                else {
                    self._waits = self._waits.filter(x => x != flag);
                    // self._waits.splice(index, 1);
                    ad.hide().then(() => {

                    }).catch(() => {
                        // self.hideCustomAd(flag);
                        tyqSDK.log("hide");
                        ad.hide().catch(() => {
                            tyqSDK.log("hide");
                        });
                    });
                }

            });
            ad.onError((res) => {
                tyqSDK.log(':', res.errCode, ' ', res.errMsg);
                if (parseInt(res.errCode) === 1004 && this.cb) {
                    tyqSDK.log("");
                    this.cb();
                }
            });
            ad.onClose(() => {
                tyqSDK.log("");
            });
        } else {
            // 
            // wx.showModal({
            //     title: '',
            //     content: ''
            // })
        }
    }

    public static rand(min, max) {
        return min + Math.floor(Math.random() * (max - min + 1));
    }
    
    /**
     * 
     * @param flag 
     */
    public static hideCustomAd(flag) {
        let ad = null;

        tyqSDK.log("", flag, this._ads, this._waits);
        if (this._ads[flag]) {
            ad = this._ads[flag];
            if (ad.isShow())
                ad.hide().then(() => {

                }).catch(() => {
                    // self.hideCustomAd(flag);
                    tyqSDK.log("hide");
                    ad.hide().catch(() => {
                        tyqSDK.log("hide");
                    });
                });
        } else {
            if (this._waits.indexOf(flag) == -1) {
                this._waits.push(flag);
            }

        }
    }
    public static destroyCustomAd(flag) {
        if (this._ads[flag]) {
            this._ads[flag].destroy();
            this._ads[flag] = null;
        }
    }

    private static compareVersion(v1, v2) {
        v1 = v1.split('.')
        v2 = v2.split('.')
        const len = Math.max(v1.length, v2.length)

        while (v1.length < len) {
            v1.push('0')
        }
        while (v2.length < len) {
            v2.push('0')
        }

        for (let i = 0; i < len; i++) {
            const num1 = parseInt(v1[i])
            const num2 = parseInt(v2[i])

            if (num1 > num2) {
                return 1
            } else if (num1 < num2) {
                return -1
            }
        }

        return 0
    }

    /**
     * 
     * @param adSize 
     * @param pos 
     * @returns 
     */
    private static getPos(adSize: any, pos: IPos) {
        if (!this.sysInfo) {
            this.sysInfo = wx.getSystemInfoSync();
        }
        let sysInfo = this.sysInfo;
        let w = adSize.width;
        let h = adSize.height;

        let sc_w = sysInfo.windowWidth / cc.winSize.width;
        let sc_h = sysInfo.windowHeight / cc.winSize.height;

        let l = (sysInfo.windowWidth - w) / 2;
        let t = (sysInfo.windowHeight - h) / 2;
        if (pos && pos.left != undefined) l = pos.left * sc_w;
        if (pos && pos.top != undefined) t = pos.top * sc_h;
        if (pos && pos.right != undefined) l = sysInfo.windowWidth - w - pos.right * sc_w;
        if (pos && pos.bottom != undefined) t = sysInfo.windowHeight - h - pos.bottom * sc_h;
        if (pos && pos.centerX != undefined) l = (sysInfo.windowWidth - w) / 2 + pos.centerX * sc_w;
        if (pos && pos.centerY != undefined) t = (sysInfo.windowHeight - h) / 2 + pos.centerY * sc_h;

        return {
            left: l,
            top: t
        }
    }
    public static hideAllAd() {
        for (const key in this._ads) {
            this.hideCustomAd(key);
        }
    }
}