	/**
	 * @description 
	 */
	public getUserInfo(resolve?, reject?): void {
		this.getSetting((res: any) => {
			wx.getUserInfo({
				success: (res: any) => {
					if (resolve)
						resolve(res);
				},
				fail: (err) => {
					if (reject)
						reject(err);
				}
			});
		}, (res) => {
			if (reject) {
				tyqSDK.log(res);
				reject("getsetting ");
			}

		})
	}

