	/**
	 * 
	 */
	public showIconAd(dir: string) {
		if (dir == 'R') {
			if (this.RiconAD) {
				this.RiconAD.load().then(() => {
					this.RiconAD.show()
				}).catch((err) => {
					console.error(err)
				})
			} else {
				setTimeout(this.createIconAd, 1000)
			}
		} else {
			if (this.LiconAD) {
				this.LiconAD.load().then(() => {
					this.LiconAD.show()
				}).catch((err) => {
					console.error(err)
				})
			} else {
				setTimeout(this.createIconAd, 1000)
			}
		}
	}

