    private request(url:string){
        return new Promise((resolve,reject)=>{
            let xhr = new XMLHttpRequest();
            xhr.timeout = 1000*3 ;
            xhr.onreadystatechange = () => {
                console.log("statusText:",xhr.statusText);
                if (xhr.readyState === 4 && (xhr.status >= 200 && xhr.status < 300)) {
                    let respone = xhr.responseText;
                    if (!respone) {
                        console.log('http error', respone);
                        return;
                    }
                    console.log(':',respone);
                    let message = JSON.parse(respone);
                    // if (message.result == 200) {
                    //     // console.log("serverTime:",message.serverTime);
                        
                    // }
                    // else{
                    //     reject({result:false})
                    // }
                    if (message.serverTime) {
                        let time = Number(message.serverTime);
                        resolve({result:true,time:time});
                    }
                    else{
                        reject({result:false})
                    }
                    
                }
                // else{
                //     reject({result:false})
                // }
            };

            xhr.ontimeout = (res) => {
                // console.log('ontimeout:', res);
                reject({result:false})
                // NetMgr.getInstance ().repeatNet(mid);
            };

            xhr.onerror = (error) => {
                // 
                // console.log('http.POST Error', error);
                reject({result:false})
            };
            // console.log('url:', url);
            xhr.open('POST', url, true);
            xhr.setRequestHeader('Content-Type', 'application/json');
            

            // xhr.send(JSON.stringify(msg));
            xhr.send();
        })
    }



