
/**
 * String.format
 * cc.d.ts
declare interface String {
    format(...agrs: any[]): string;
    parseColor();
}
 */
String.prototype.format = function () {
    let str = this;
    for (let i in arguments) {
        str = str.replace(/\%\w/, arguments[i]);
    }
    return str;
}
