export const utilTools = {

    getRandomSign() {
        return Math.random() > 0.5 ? 1 : -1;
    },

    // 
    radianToAngle(radian: number) {
        return radian / Math.PI * 180;
    },
    angleToRadian(angle: number) {
        return angle / 180 * Math.PI;
    },

    // 
    forArr(arr: any, cb: Function) {
        let len = arr.length;
        if (len <= 0) {
            return;
        }
        for (let i = 0; i < len; i++) {
            let info = arr[i];
            if (info == undefined) {
                continue;
            }
            let ret = cb(info, i);
            if (ret) {
                break;
            }
        }
    },

    /**
     * async.waterfall
     * @param taskArr 
     * @param callback 
     */
    waterfall(taskArr: any, callback?: any) {
        if (!callback) {
            callback = function () { };
        }
        if (!Array.isArray(taskArr) || taskArr.length <= 0) {
            callback("taskArr is null");
            return;
        }
        let taskIndex = 0;
        let result = {};
        let loadNextTask = function () {
            let task = taskArr[taskIndex];
            if (task) {
                task(result, function (err) {
                    if (err) {
                        callback(err, null);
                        return;
                    }
                    taskIndex++;
                    loadNextTask();
                });
                return;
            }
            callback(null, result);
        };
        loadNextTask();
    },

    // 
    valToPercent(val: number, fixNum?: number) {
        // 
        val = Math.floor(val * 10000);
        val = val / 10000;

        if (fixNum == undefined) {
            fixNum = 2;
        }
        let ret = (val * 100).toFixed(fixNum) + "%";

        return ret;
    },

    /**
     * 
     * 05:00:56
     * @param d 
     * @param isM 
     * @returns {string}
     */
    getTimeStr(d: number, isM?: boolean) {
        var ret = "";
        if (d < 0) {
            d = 0;
        }

        d = d / 1000;
        var hours = Math.floor(d / 3600);
        var mins = Math.floor((d - hours * 3600) / 60);
        var seconds = Math.floor(d - hours * 3600 - mins * 60);

        if (isM) {
            mins = mins + hours * 60;
        } else {
            if (hours >= 10) {
                ret = ret + hours + ":";
            } else {
                ret = ret + "0" + hours + ":";
            }
        }

        if (mins >= 10) {
            ret = ret + mins + ":";
        } else {
            ret = ret + "0" + mins + ":";
        }
        if (seconds >= 10) {
            ret = ret + seconds;
        } else {
            ret = ret + "0" + seconds;
        }

        return ret;
    },

    /**
       * 
       * 05:00:56
       * @param d 
       * @param isM 
       * @returns {string}
       */
    getTimeStrChinese(d: number, isSce = true, isM = true, isHour = true) {
        var ret = "";
        if (d < 0) {
            d = 0;
        }

        d = d / 1000;
        var hours = Math.floor(d / 3600);
        var mins = Math.floor((d - hours * 3600) / 60);
        var seconds = Math.floor(d - hours * 3600 - mins * 60);


        if (hours > 0) {
            ret = ret + hours + "";
        }

        if (mins >= 10) {
            ret = ret + mins + "";
        } else {
            ret = ret + "0" + mins + "";
        }
        if (isSce) {
            if (seconds >= 10) {
                ret = ret + seconds + "";
            } else {
                ret = ret + "0" + seconds + "";
            }
        }

        return ret;
    },

    /**
     * objtime()keyjsDate
     * @param obj
     */
    timeStrToDate(obj: any) {
        if (typeof (obj) != "object" || obj == null) {
            return obj;
        }

        if (Array.isArray(obj)) {
            for (let i in obj) {
                let item = obj[i];
                if (item && typeof (item) == "object") {
                    utilTools.timeStrToDate(item);
                }
            }
        } else {
            let keyArr = Object.keys(obj);
            for (let i in keyArr) {
                let key = keyArr[i] + "";
                let val = obj[key];
                if (val && typeof (val) == "object") {
                    utilTools.timeStrToDate(val);
                    continue;
                }

                try {
                    let endStr = "";
                    if (key.length >= 4) {
                        endStr = key.substring(key.length - 4, key.length);
                        if (endStr.toLocaleLowerCase() == "time" && val && typeof (val) == "string") {
                            obj[key] = new Date(val);
                        }
                    }
                } catch (e) {
                    console.log("utilTools.timeStrToDate error:" + e.message, e, obj);
                }

            }
        }

        return obj;
    },

    /**
     * 
     * @param arr 
     * @param compareFunc 
     */
    sortArr(arr: any, compareFunc: Function) {
        if (!arr || arr.length <= 1) {
            return arr;
        }
        for (let i = 0; i < arr.length - 1; i++) {
            for (let j = i + 1; j < arr.length; j++) {
                let item1 = arr[i];
                let item2 = arr[j];
                if (compareFunc(item1, item2)) {
                    arr[i] = item2;
                    arr[j] = item1;
                }
            }
        }
    },

    /**
     * 
     * @param val  01
     */
    isProb(val: number) {
        if (!val) {
            val = 0;
        }
        if (Math.random() < val) {
            return true;
        }
        return false;
    },

    /**
     * 
     * @param arr 
     * @param isOrign true:
     */
    randomArr(arr: any, isOrign: boolean = false): any {
        if (!isOrign) {
            arr = JSON.parse(JSON.stringify(arr));
        }
        let tmpArr = [];
        while (arr.length > 0) {
            let index = Math.floor(Math.random() * arr.length);
            tmpArr.push(arr[index]);
            arr.splice(index, 1);
        }
        return tmpArr;
    },

    /**
     * ,
     * @param min 
     * @param max 
     * @param isInt 
     * @returns {number}
     */
    getFloatValue(min: number, max: number, isInt?: boolean) {
        let dv = max - min;
        if (dv == 0) {
            return min;
        }
        if (dv < 0) {
            return 0;
        }
        if (isInt) {
            let val = min + Math.floor(Math.random() * (dv + 1));
            return val;
        }

        let val = min + Math.random() * dv;
        return val;
    },

    // 
    overDay(date: any, now?: any) {
        if (!date) {
            return true;
        }

        if (!now) {
            now = new Date();
        }
        let overDay = false;
        if (date.getDate() == now.getDate()) {
            if (now.getTime() - date.getTime() >= 24 * 60 * 60 * 1000) {
                overDay = true;
            }
        } else {
            overDay = true;
        }

        return overDay;
    },

    /**
     * 
     * @param arr 
     * @param num 
     * @param canRepeat 
     * @returns {Array} 
     */
    getRowsByWeightAndNum(arr: any, num: number, canRepeat?: boolean) {
        if (!num) {
            num = 1;
        }
        var ret = [];
        arr = utilTools.copyObj(arr);
        for (var i = 0; i < num; i++) {
            if (arr.length <= 0) {
                break;
            }
            var row = this.getRowByWeight(arr);
            ret.push(row);
            if (!canRepeat) {
                var index = arr.indexOf(row);
                arr.splice(index, 1);
            }
        }
        return ret;
    },

    /**
     * 
     * @param arr
     * @param totalWeight
     */
    getRowByWeight(arr: any, totalWeight?: number) {
        if (!totalWeight) {
            totalWeight = 0;
            for (var i in arr) {
                var weight = arr[i].weight;
                if (!weight) {
                    weight = 0;
                }
                totalWeight += weight;
            }
        }
        var random = Math.random() * totalWeight;
        var left = 0;
        var row = null;
        for (var i in arr) {
            var tmpRow = arr[i];
            var weight = tmpRow.weight;
            if (!weight) {
                weight = 0;
            }
            if (random >= left && random < left + weight) {
                row = tmpRow;
                break;
            }
            left += weight;
        }

        return row;
    },

    /**
     * 
     * @param arr 
     * @param isRemove 
     * @return {null|*} 
     */
    getRandomItemByArr(arr: any, isRemove: boolean = false) {
        if (!arr || arr.length <= 0) {
            return null;
        }
        let index = Math.floor(Math.random() * arr.length);
        let item = arr[index];
        if (isRemove) {
            arr.splice(index, 1);
        }

        return item;
    },

    /**
     * 
     * @param obj 
     */
    copyObj(obj: object) {
        return JSON.parse(JSON.stringify(obj));
    },

}

